const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&â€™*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./targetlist.component.html",
    styleUrls: ['./targetlist.component.css'],
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    FLY_IN_OUT_ANIMATION, ROUND_ANTICLOCK_ANIMATION
  ],
})
export class TargetListComponent implements OnInit,AfterViewInit {
	 @ViewChild(MatSort) sort: MatSort; 
	 @ViewChild(ListComponent)
	
//	 private list: ListComponent;
	 targetForm: FormGroup; 
	 targetUpdateForm: FormGroup; 
	  
	targetList: TargetList[];
	  demo = [];
	  targetRouting :TargetRouting[];
	  targetResponseObj = [];
	  targetCount = {};
	  targetThead = [];
	  formVariables = ["opprCount","opprCalls","opprMeeting","opprDemos","opprHot","opprSo","opprSOAmount","modelId"];
	  targetModal = ["Model1","fastrack","new1","mine","Swift","Omni","i20"];
	  targetFormControlName:TargetFormControl[]=[];
	  targetCountData = [];
	listHide: boolean = true;
	listPageHide: boolean = true;
	addPageHide: boolean = true;
	animateAdd: string = 'out';
	animateList: string = 'in';
	 
	targetId = 0;  
	pageValue: string ="1";
	sortField: string ="";
	sortDirection: string ="";
	length = 10;
     
	pageIndex: number=1;
	ajaxData : targetData[];
	target = new targetData();
	  
	  targetemp =[];
	  targetyear =[
		  {key: '1', name: '2007'},
		  {key: '2', name: '2008'},
		  {key: '3', name: '2009'},
		  {key: '4', name: '2010'},
		  {key: '5', name: '2011'},
		  {key: '6', name: '2012'},
		  {key: '7', name: '2013'},
		  {key: '8', name: '2014'},
		  {key: '9', name: '2015'},
		  {key: '10', name: '2016'},
		  {key: '11', name: '2017'},
		  {key: '12', name: '2018'},
		  {key: '13', name: '2019'},
		  {key: '14', name: '2020'},
		  
	  ];
    constructor(private _script: ScriptLoaderService, 	private http: HttpClient,) { }

    ngOnInit() {
		this.listPageHide = false;
		this.listHide = true;
		
		const jobGroup: FormGroup = new FormGroup({});
		this.targetUpdateForm = jobGroup;

		const headers = new HttpHeaders({'Content-Type':'application/json'});
			const demo = this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/executive',
							 			JSON.stringify({ execuitiveId :'1'}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.targetemp = data['populateExecutive'];
				   														
			  															}); 
		
			this.targetForm = new FormGroup({	
				'execuitiveId' : new FormControl('',[ Validators.required] ), 
				'year' : new FormControl('',[ Validators.required] ), 
			});
		
		
		
		
		
		}
	  
	  
	  
	  get execuitiveId(){ return this.targetForm.get('execuitiveId')};  
	  get year(){ return this.targetForm.get('year')};  
	  
	  
	  toggleAnimation(){
		  alert();
		if(this.animateAdd == 'out' || this.animateList == 'in'){
			this.listPageHide = true;
			this.listHide = true;
			this.addPageHide = false;
//			if(this.targetId == 0){
//				this.populateDropDownData();
//			}else{
//				this.populateData();
//			}
			this.animateList = 'out';
			this.animateAdd = 'in';
		}else{
			this.addPageHide = true;
			this.listPageHide = false;
			this.listHide = false;
			this.targetForm.reset();
			this.animateAdd= 'out';
			this.animateList = 'in';
		}
	  }

	  
	  ngAfterViewInit() { 
//	  	this.list.listUrl="http://192.168.0.14:8060/axelacrm/sales/target-list";
		  
		  
		  /*{modal:"Model1",'fc':["fc1","fc2","fc3","fc4","fc5","fc6","fc7"]},
							  {modal:"fastrack",'fc':["fc8","fc9","fc10","fc11","fc12","fc13","fc14"]},
							  {modal:"new1",'fc':["fc15","fc16","f17","f18","f19","fc20","fc21"]},
							  {modal:"mine",'fc':["fc22","fc23","fc24","fc25","fc26","fc27","fc28"]},
							  {modal:"Swift",'fc':["fc29","fc30","fc31","fc32","fc33","fc34","fc35"]},
							  {modal:"Omni",'fc':["fc36","fc37","fc38","fc39","fc40","fc41","fc42"]},
							  {modal:"i20",'fc':["fc43","fc44","fc45","fc46","fc47","fc48","fc49"]}*/
		  
	  }
	  
	  
	  onSelectYear(year,executiveId)
	  {
//		  this.targetForm.submit();
		  if(!this.targetForm.invalid)
		  {
		   const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.target.year = year;
		  this.target.execuitiveId = executiveId;
		  this.target.year = year;
//		    alert("year=="+year+"exe==="+executiveId);
		  this.listHide = false;
		   this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/target-list',
									JSON.stringify( { 'year' : year, 'executiveId': executiveId}),

						{ headers, withCredentials: true }).subscribe(data => {
			   											this.listHide = false;
			   											this.targetThead=data['heading'];
			   											this.targetList=data['targetList'];
			   											this.targetRouting = data['targetRouting'];
			  											 this.targetCount = data['targetCount'][0];
														});
		  }
	  }
	     
	  
	   onSelect(executiveId, year)
	   {
		  if(!this.targetForm.invalid)
		  {   
		    const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.target.execuitiveId = executiveId;
		  this.target.year = year;
		   this.listHide = false;
		   this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/target-list',
									JSON.stringify( { 'year' : year, 'executiveId': executiveId }),

						{ headers, withCredentials: true }).subscribe(data => {
			 											console.log(this.target.execuitiveId);
			   											this.listHide = false;
			   											this.targetList=data['targetList'];
			   											this.targetRouting = data['targetRouting'];
			   											this.targetCount = data['targetCount'][0];
														});
			}
	   }
	  
	  onClick(index:number){
		  
		  this.targetFormControlName = [];
		 const jobGroup: FormGroup = new FormGroup({});
											  for(var i=0;i<68;i++){
												  this.targetFormControlName.push({modal:"",fc:[]});
												  for(var j=0;j<this.formVariables.length;j++){
													  this.targetFormControlName[i].fc[j]=this.formVariables[j]+i;
													  this.demo[j]=this.formVariables[j]+i;
													  const control: FormControl = new FormControl('');
												  jobGroup.addControl(this.demo[j],control);
												  }
											  }
			   								  this.targetUpdateForm = jobGroup;
											  this.getFormControl(index);
											  this.toggleAnimation();
	  }
	  
	  getFormControl(index:number){
		  this.targetResponseObj[0] = this.targetRouting[index];
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/target-model-list',
									JSON.stringify(this.targetRouting[index]),

						{ headers, withCredentials: true }).subscribe(data => {
			 											this.targetCountData = data['targetPopulate'];
			   											this.targetThead = data['heading']; 
			   											this.targetCount = data['targetCount']; 
														});
			   											
		
		  
	}
	  
	  onSubmit(event) {
		  this.targetResponseObj[1] = this.targetUpdateForm.value;
		  this.targetResponseObj[2] = {requestType:'update'};
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post('http://192.168.0.15:7040/axelacrm/sales/sales-target',
									JSON.stringify(this.targetResponseObj),

						{ headers, withCredentials: true }).subscribe(data => {
			 											this.toggleAnimation();
			   											this.getListData(this.targetResponseObj[0].executiveId, this.targetResponseObj[0].year);
			   											
														});
//		  this.toggleAnimation();
	  }
	  
	  getListData(executiveId, year){
		  alert("executiveId===="+executiveId+";;;;;;;;;;"+"year========"+year);
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.target.execuitiveId = executiveId;
		  this.target.year = year;
		   this.listHide = false;
		   this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/target-list',
									JSON.stringify( { 'year' : year, 'executiveId': executiveId }),

						{ headers, withCredentials: true }).subscribe(data => {
			 											console.log(this.target.execuitiveId);
			   											this.listHide = false;
			   											this.targetList=data['targetList'];
			   											this.targetRouting = data['targetRouting'];
			   											this.targetCount = data['targetCount'][0];
														});
	  }
}  

	export class TargetList{
		targetSoAmount: string="";
		month: string="";
		targetOpprDemosCount: string="";
		targetOpprCallsCount: string="";
		targetOpprCount: string="";
		targetOpprMeetingsCount: string="";
		targetOpprHotCount: string="";
		targetSoCount: string="";
	}

export class TargetRouting{
		targetId: string="";
		month: string="";
		year: string="";
		executiveId: string="";
	}
	
	export class TargetFormControl{
		modal: string="";
		fc: string[];
	}
	
export class targetData { 
 execuitiveId : string;
 year = new Date().getFullYear().toString(); 
}



